import { Component, OnInit} from '@angular/core';
import { Router } from '@angular/router';
import { trigger, state, style, animate, transition } from '@angular/animations';
import { Location } from '@angular/common'

//   2021-01-06 
import { ShRedirectService} from 'sh-http-client';
import { ShAppComponent } from "sh-form-control";
//   2021-01-06 

const openClose = trigger('openClose', [
  state('open', style({
    margin: 0
  })),
  state('closed', style({
    marginTop: '-300%'
  })),
  transition('open => closed', [
    animate('0.2s')
  ]),
  transition('closed => open', [
    animate('0.1s')
  ]),
])

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css'],
  animations: [
    openClose
  ]
})
//   2021-01-06ShAppComponent  
//export class AppComponent implements OnInit {
export class AppComponent extends ShAppComponent implements OnInit {
//   2021-01-06ShAppComponent 

  // 
  displayTitle: string = "";
  rootPath: string = "";
  frmPath: string = "";
  show: boolean = false;
  message_str: String = "";
  errorMessages: String[];
  errorFlg: boolean;
  mode: number;
  systemDate: Date = new Date();

  day: string[] = ["", "", "", "", "", "", ""];

  approvalLevels: number;

  loginUser: string;
  branchName: string;

  constructor( 
    private router: Router, 
    private location: Location,
//   2021-01-06  
    protected shRedirectService   : ShRedirectService
//   2021-01-06  
    ) { 

//   2021-01-06 (ShAppComponent) 
      super(shRedirectService);
//   2021-01-06 (ShAppComponent) 

      //TODO:
      this.approvalLevels = 3;
      //TODO: 
      this.loginUser = '';
      this.branchName = ''; 
    }

  ngOnInit() {

    //historynull
    //
    this.location.onUrlChange((event) => {
      setTimeout(() => {
        history.pushState(null, null, null);
      }, 0);
    });

    //historynull
    window.onpopstate = function(event) {
      history.pushState(null, null, null);
      window.alert('');

    };
    window.document.onkeydown = function (event) {
      if (
        event.keyCode == 112 ||
        event.keyCode == 113 ||
        event.keyCode == 114 ||
        event.keyCode == 115 ||
        event.keyCode == 116 ||
        event.keyCode == 117 ||
        event.keyCode == 122
      ) {
        return false;
      };
      if (event.altKey && (event.keyCode == 37 || event.keyCode == 39)) {
        return false;
      }
      //Chrome
      if ((event.ctrlKey || event.metaKey) && event.keyCode == 78) {
        return false;
      }
      if ((event.ctrlKey || event.metaKey) && event.keyCode == 82) {
        return false;
      }
      if ((event.ctrlKey || event.metaKey) && event.keyCode == 68) {
        return false;
      }
    };
  }
  /**
   *  
   * @param strTitle 
   * @param strPath 
   */
  setHeader(strTitle:string, strPath:string = "") {
    this.displayTitle = strTitle;
    this.rootPath = strPath;
  }

  /**
   * 
   */
  getSystemDate(): string {
    return `${this.systemDate.toLocaleDateString()}(${this.day[this.systemDate.getDay()]}) ${this.systemDate.toLocaleTimeString().slice(0, -3)}`;
  }

  ngOnDestroy(): void {

    sessionStorage.clear();
    
  }

}
